import { IonButton, IonButtons, IonCard, IonCardContent, IonCardHeader, IonCol, IonContent, IonGrid, IonHeader, IonIcon, IonInput, IonItem, IonLabel, IonMenuButton, IonPage, IonRow, IonTitle, IonToolbar } from '@ionic/react';
import { add, checkmark, pencil, trash } from 'ionicons/icons';
import { useEffect, useState } from 'react';
import { useHistory, useParams, useRouteMatch } from 'react-router';
import ExploreContainer from '../../components/ExploreContainer';
import Empleado from './Proveedor';
import { guardarProveedor, searchProveedorById } from './ProveedorApi';

const ProveedorEditar: React.FC = () => {

  const { name } = useParams<{ name: string; }>();

  const [proveedor, setProveedor] = useState<any>({});
  const history = useHistory();

  const routeMatch:any = useRouteMatch("/page/proveedor/:id");
  const id = routeMatch?.params?.id;

  //Se ejecuta esta función al momento de entrar en la página
  useEffect(()=>{
    search();
  },[history.location.pathname]);

  const search = async () =>{
    if(id == 'new') {
      setProveedor({});
    }else{
      let result = await searchProveedorById(id);
      setProveedor(result);
    }
  }

  const guardar = () => {
    //provisional 
    //cliente.id = Math.round(Math.random()*10000);
    console.log(proveedor);
    guardarProveedor(proveedor);
    history.push('/page/proveedores')
  }



  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonButtons slot="start">
            <IonMenuButton />
          </IonButtons>
          <IonTitle>{name}</IonTitle>
        </IonToolbar>
      </IonHeader>

      <IonContent fullscreen>
        <IonHeader collapse="condense">
          <IonToolbar>
            <IonTitle size="large">{name}</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonCard>
          <IonCardHeader>
            <IonTitle>{id === 'new' ? 'Agregar proveedor' : 'Editar proveedor'}</IonTitle>
            <IonRow>
              <IonCol>
                <IonItem>
                  <IonLabel position="stacked">Nombre</IonLabel>
                  <IonInput onIonChange={e => proveedor.nombre = String(e.detail.value)} value={proveedor.nombre}></IonInput>
                </IonItem>
              </IonCol>
              <IonCol>
                <IonItem>
                  <IonLabel position="stacked">Email</IonLabel>
                  <IonInput
                    onIonChange={e => proveedor.email = String(e.detail.value)}
                    value={proveedor.email}>
                  </IonInput>
                </IonItem>
              </IonCol>
            </IonRow>
            <IonRow>
              <IonCol>
                <IonItem>
                  <IonLabel position="stacked">Teléfono</IonLabel>
                  <IonInput onIonChange={e => proveedor.telefono = String(e.detail.value)} value={proveedor.telefono}></IonInput>
                </IonItem>
              </IonCol>
              <IonCol>
                <IonItem>
                  <IonLabel position="stacked">Dirección</IonLabel>
                  <IonInput onIonChange={e => proveedor.direccion = String(e.detail.value)} value={proveedor.direccion}></IonInput>
                </IonItem>
              </IonCol>
            </IonRow>
            <IonItem>
              <IonButton onClick={guardar} color='success' slot='end'>
                <IonIcon icon={checkmark} />
                Guardar
              </IonButton>
            </IonItem>
          </IonCardHeader>
          <IonCardContent>

          </IonCardContent>
        </IonCard>
      </IonContent>
    </IonPage>
  );
};

export default ProveedorEditar;
