import { IonButton, IonButtons, IonCard, IonCardContent, IonCardHeader, IonCol, IonContent, IonGrid, IonHeader, IonIcon, IonInput, IonItem, IonLabel, IonMenuButton, IonPage, IonRow, IonTitle, IonToolbar } from '@ionic/react';
import { add, checkmark, pencil, trash } from 'ionicons/icons';
import { useEffect, useState } from 'react';
import { useHistory, useParams, useRouteMatch } from 'react-router';
import ExploreContainer from '../../components/ExploreContainer';
import Cliente from './Cliente';
import { buscarClientes, eliminarCliente, guardarCliente, searchClienteById } from './ClienteApi';

const ClienteEditar: React.FC = () => {

  const { name } = useParams<{ name: string; }>();
  
  const [cliente, setCliente] = useState<any>({});
  const history = useHistory();

  const routeMatch:any = useRouteMatch("/page/cliente/:id");
  const id = routeMatch?.params?.id;

  //Se ejecuta esta función al momento de entrar en la página
  useEffect(()=>{
    search();
  },[history.location.pathname]);

  const search = async () =>{
    if(id == 'new') {
      setCliente({});
    }else{
      let result = await searchClienteById(id);
      setCliente(result);
    }
  }

  const guardar = async () => {
    //provisional 
    //cliente.id = Math.round(Math.random()*10000);
    console.log(cliente);
    await guardarCliente(cliente);
    history.push('/page/clientes')
  }



  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonButtons slot="start">
            <IonMenuButton />
          </IonButtons>
          <IonTitle>{name}</IonTitle>
        </IonToolbar>
      </IonHeader>

      <IonContent fullscreen>
        <IonHeader collapse="condense">
          <IonToolbar>
            <IonTitle size="large">{name}</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonCard>
          <IonCardHeader>
            <IonTitle>{id === 'new' ? 'Agregar cliente' : 'Editar cliente'}</IonTitle>
            <IonRow>
              <IonCol>
                <IonItem>
                  <IonLabel position="stacked">Nombre</IonLabel>
                  <IonInput onIonChange={e => cliente.nombre = String(e.detail.value)} value={cliente.nombre}></IonInput>
                </IonItem>
              </IonCol>
              <IonCol>
                <IonItem>
                  <IonLabel position="stacked">Email</IonLabel>
                  <IonInput
                    onIonChange={e => cliente.email = String(e.detail.value)}
                    value={cliente.email}>
                  </IonInput>
                </IonItem>
              </IonCol>
            </IonRow>
            <IonRow>
              <IonCol>
                <IonItem>
                  <IonLabel position="stacked">Teléfono</IonLabel>
                  <IonInput onIonChange={e => cliente.telefono = String(e.detail.value)} value={cliente.telefono}></IonInput>
                </IonItem>
              </IonCol>
              <IonCol>
                <IonItem>
                  <IonLabel position="stacked">Dirección</IonLabel>
                  <IonInput onIonChange={e => cliente.direccion = String(e.detail.value)} value={cliente.direccion}></IonInput>
                </IonItem>
              </IonCol>
            </IonRow>
            <IonItem>
              <IonButton onClick={guardar} color='success' slot='end'>
                <IonIcon icon={checkmark} />
                Guardar
              </IonButton>
            </IonItem>
          </IonCardHeader>
          <IonCardContent>

          </IonCardContent>
        </IonCard>
      </IonContent>
    </IonPage>
  );
};

export default ClienteEditar;
