import { APP_EMPLEADOS } from "../../variables/variables";
import Empleado from "./Empleado";

export async function buscarEmpleados(){

    let url= APP_EMPLEADOS + 'empleados';
    let response = await fetch(url, {
        "method":'GET',
        "headers": {
            "Content-Type": 'application/json'
        }
    })

    let empleados = (await response).json();
    return empleados;
    
}

export async function eliminarEmpleado(id:string){
    let url= APP_EMPLEADOS + 'empleados/' + id;
    await fetch(url, {
        "method":'DELETE',
        "headers": {
            "Content-Type": 'application/json'
        }
    });
}

export async function guardarEmpleado(empleado:Empleado){
    let url= APP_EMPLEADOS + 'empleados';
    await fetch(url, {
        "method":'POST',
        "body":JSON.stringify(empleado),
        "headers": {
            "Content-Type": 'application/json'
        }
    }); 
}

export async function searchEmpleadoById(id:string){
    let url= APP_EMPLEADOS + 'empleados/' + id;
    let response = await fetch(url, {
        "method":'GET',
        "headers": {
            "Content-Type": 'application/json'
        }
    })

    let empleado = (await response).json();
    return empleado;
}