import { IonButton, IonButtons, IonCard, IonCardContent, IonCardHeader, IonCol, IonContent, IonGrid, IonHeader, IonIcon, IonItem, IonMenuButton, IonPage, IonRow, IonTitle, IonToolbar } from '@ionic/react';
import { add, pencil, trash } from 'ionicons/icons';
import { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router';
import ExploreContainer from '../../components/ExploreContainer';
import Empleado from './Empleado';
import { buscarEmpleados, eliminarEmpleado } from './EmpleadoApi';

const ListaEmpleado: React.FC = () => {

  const { name } = useParams<{ name: string; }>();
  const history = useHistory();

  //Datos de prueba
  const [empleados, setEmpleados] = useState<Empleado[]>([]);

  //Se ejecuta esta función al momento de entrar en la página
  useEffect(()=>{
    search();
  },[history.location.pathname]); //esto para que se actualice la página

  const search = async () =>{
    let result = await buscarEmpleados();
    setEmpleados(result);
  }

  const remove = async (id:string) => {
    await eliminarEmpleado(id);
    search();
  }

  const agregarEmpleado = () => {
    history.push('/page/empleado/new');
  }

  const editarEmpleado = (id:string) => {
    console.log(id);
    history.push('/page/empleado/' + id)
  }

  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonButtons slot="start">
            <IonMenuButton />
          </IonButtons>
          <IonTitle>{name}</IonTitle>
        </IonToolbar>
      </IonHeader>

      <IonContent fullscreen>
        <IonHeader collapse="condense">
          <IonToolbar>
            <IonTitle size="large">{name}</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonCard>
          <IonCardHeader>
            <IonTitle>Gestión de Empleados</IonTitle>
            <IonItem>
              <IonButton onClick={agregarEmpleado} fill='outline' slot='end'>
                <IonIcon icon={add} />
                Agregar empleado
              </IonButton>
            </IonItem>
          </IonCardHeader>
          <IonCardContent>
            <IonGrid className='table tablee'>
              <IonRow >
                <IonCol size="12" size-sm="2">Nombre</IonCol>
                <IonCol size="12" size-sm="2">Email</IonCol>
                <IonCol size="12" size-sm="2">Teléfono</IonCol>
                <IonCol size="12" size-sm="2">Dirección</IonCol>
                <IonCol size="12" size-sm="2">Salario</IonCol>
                <IonCol size="12" size-sm="2">Acciones</IonCol>
              </IonRow>
              {empleados.map((empleado: Empleado) => 
                <IonRow >
                  <IonCol size="12" size-sm="2">{empleado.nombre}</IonCol>
                  <IonCol size="12" size-sm="2">{empleado.email}</IonCol>
                  <IonCol size="12" size-sm="2">{empleado.telefono}</IonCol>
                  <IonCol size="12" size-sm="2">{empleado.direccion}</IonCol>
                  <IonCol size="12" size-sm="2">{empleado.salario}</IonCol>
                  <IonCol size="12" size-sm="2">
                    <IonButton onClick={() => editarEmpleado(String(empleado.id))} color="warning">
                      <IonIcon icon={pencil}/>
                    </IonButton>
                    <IonButton color="danger" onClick={() => remove(String(empleado.id))}>
                      <IonIcon icon={trash}/>
                    </IonButton>
                  </IonCol>
                </IonRow>
                )}
            </IonGrid>
          </IonCardContent>
        </IonCard>
      </IonContent>
    </IonPage>
  );
};

export default ListaEmpleado;
