import { IonButton, IonButtons, IonCard, IonCardContent, IonCardHeader, IonCol, IonContent, IonGrid, IonHeader, IonIcon, IonItem, IonMenuButton, IonPage, IonRow, IonTitle, IonToolbar } from '@ionic/react';
import { add, pencil, trash } from 'ionicons/icons';
import { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router';
import ExploreContainer from '../../components/ExploreContainer';
import Proveedor from './Proveedor';
import { buscarProveedores, eliminarProveedor } from './ProveedorApi';

const ListaProveedor: React.FC = () => {

  const { name } = useParams<{ name: string; }>();
  const history = useHistory();

  //Datos de prueba
  const [proveedores, setProveedores] = useState<Proveedor[]>([]);

  //Se ejecuta esta función al momento de entrar en la página
  useEffect(()=>{
    search();
  },[history.location.pathname]); //esto para que se actualice la página

  const search = async () =>{
    let result = await buscarProveedores();
    setProveedores(result);
  }

  const remove = (id:string) => {
    eliminarProveedor(id);
    search();
  }

  const agregarProveedor = () => {
    history.push('/page/proveedor/new');
  }

  const editarProveedor = (id:string) => {
    console.log(id);
    history.push('/page/proveedor/' + id)
  }

  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonButtons slot="start">
            <IonMenuButton />
          </IonButtons>
          <IonTitle>{name}</IonTitle>
        </IonToolbar>
      </IonHeader>

      <IonContent fullscreen>
        <IonHeader collapse="condense">
          <IonToolbar>
            <IonTitle size="large">{name}</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonCard>
          <IonCardHeader>
            <IonTitle>Gestión de Proveedores</IonTitle>
            <IonItem>
              <IonButton onClick={agregarProveedor} fill='outline' slot='end'>
                <IonIcon icon={add} />
                Agregar proveedor
              </IonButton>
            </IonItem>
          </IonCardHeader>
          <IonCardContent>
            <IonGrid className='table tablep'>
              <IonRow >
                <IonCol size="12" size-sm="2">Nombre</IonCol>
                <IonCol size="12" size-sm="3">Email</IonCol>
                <IonCol size="12" size-sm="2">Teléfono</IonCol>
                <IonCol size="12" size-sm="3">Dirección</IonCol>
                <IonCol size="12" size-sm="2">Acciones</IonCol>
              </IonRow>
              {proveedores.map((proveedor: Proveedor) => 
                <IonRow >
                  <IonCol size="12" size-sm="2">{proveedor.nombre}</IonCol>
                  <IonCol size="12" size-sm="3">{proveedor.email}</IonCol>
                  <IonCol size="12" size-sm="2">{proveedor.telefono}</IonCol>
                  <IonCol size="12" size-sm="3">{proveedor.web}</IonCol>
                  <IonCol size="12" size-sm="2">
                    <IonButton onClick={() => editarProveedor(String(proveedor.id))} color="warning">
                      <IonIcon icon={pencil}/>
                    </IonButton>
                    <IonButton color="danger" onClick={() => remove(String(proveedor.id))}>
                      <IonIcon icon={trash}/>
                    </IonButton>
                  </IonCol>
                </IonRow>
                )}
            </IonGrid>
          </IonCardContent>
        </IonCard>
      </IonContent>
    </IonPage>
  );
};

export default ListaProveedor;
