import { IonButton, IonButtons, IonCard, IonCardContent, IonCardHeader, IonCol, IonContent, IonGrid, IonHeader, IonIcon, IonItem, IonMenuButton, IonPage, IonRow, IonTitle, IonToolbar } from '@ionic/react';
import { add, pencil, trash } from 'ionicons/icons';
import { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router';
import ExploreContainer from '../../components/ExploreContainer';
import Cliente from './Cliente';
import { buscarClientes, eliminarCliente } from './ClienteApi';

const ListaCliente: React.FC = () => {

  const { name } = useParams<{ name: string; }>();
  const history = useHistory();

  //Datos de prueba
  const [clientes, setClientes] = useState<Cliente[]>([]);

  //Se ejecuta esta función al momento de entrar en la página
  useEffect(()=>{
    search();
  },[history.location.pathname]); //esto para que se actualice la página

  const search = async () =>{
    let result = await buscarClientes();
    setClientes(result);
  }

  const remove = async (id:string) => {
    await eliminarCliente(id);
    search();
  }

  const agregarCliente = () => {
    history.push('/page/cliente/new');
  }

  const editarCliente = (id:string) => {
    console.log(id);
    history.push('/page/cliente/' + id)
  }

  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonButtons slot="start">
            <IonMenuButton />
          </IonButtons>
          <IonTitle>{name}</IonTitle>
        </IonToolbar>
      </IonHeader>

      <IonContent fullscreen>
        <IonHeader collapse="condense">
          <IonToolbar>
            <IonTitle size="large">{name}</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonCard>
          <IonCardHeader>
            <IonTitle>Gestión de Clientes</IonTitle>
            <IonItem>
              <IonButton onClick={agregarCliente} fill='outline' slot='end'>
                <IonIcon icon={add} />
                Agregar cliente
              </IonButton>
            </IonItem>
          </IonCardHeader>
          <IonCardContent>
            <IonGrid className='table tablec'>
              <IonRow color='dark'>
                <IonCol size="12" size-sm="2">Nombre</IonCol>
                <IonCol size="12" size-sm="3">Email</IonCol>
                <IonCol size="12" size-sm="2">Teléfono</IonCol>
                <IonCol size="12" size-sm="3">Dirección</IonCol>
                <IonCol size="12" size-sm="2">Acciones</IonCol>
              </IonRow>
              {clientes.map((cliente: Cliente) => 
                <IonRow >
                  <IonCol size="12" size-sm="2">{cliente.nombre}</IonCol>
                  <IonCol size="12" size-sm="3">{cliente.email}</IonCol>
                  <IonCol size="12" size-sm="2">{cliente.telefono}</IonCol>
                  <IonCol size="12" size-sm="3">{cliente.direccion}</IonCol>
                  <IonCol size="12" size-sm="2">
                    <IonButton onClick={() => editarCliente(String(cliente.id))} color="warning">
                      <IonIcon icon={pencil}/>
                    </IonButton>
                    <IonButton color="danger" onClick={() => remove(String(cliente.id))}>
                      <IonIcon icon={trash}/>
                    </IonButton>
                  </IonCol>
                </IonRow>
                )}


            </IonGrid>
          </IonCardContent>
        </IonCard>
      </IonContent>
    </IonPage>
  );
};

export default ListaCliente;
