import { IonApp, IonRouterOutlet, IonSplitPane, setupIonicReact } from '@ionic/react';
import { IonReactRouter } from '@ionic/react-router';
import { Redirect, Route } from 'react-router-dom';
import Menu from './components/Menu';
import Page from './pages/Page';

/* Core CSS required for Ionic components to work properly */
import '@ionic/react/css/core.css';

/* Basic CSS for apps built with Ionic */
import '@ionic/react/css/normalize.css';
import '@ionic/react/css/structure.css';
import '@ionic/react/css/typography.css';

/* Optional CSS utils that can be commented out */
import '@ionic/react/css/padding.css';
import '@ionic/react/css/float-elements.css';
import '@ionic/react/css/text-alignment.css';
import '@ionic/react/css/text-transformation.css';
import '@ionic/react/css/flex-utils.css';
import '@ionic/react/css/display.css';

/* Theme variables */
import './theme/variables.css';
import ListaCliente from './pages/cliente/ListaCliente';
import ClienteEditar from './pages/cliente/ClienteEditar';
import ListaEmpleado from './pages/empleado/ListaEmpleado';
import EmpleadoEditar from './pages/empleado/EmpleadoEditar';
import ListaProveedor from './pages/proveedor/ListaProveedor';
import ProveedorEditar from './pages/proveedor/ProveedorEditar';

setupIonicReact();

const App: React.FC = () => {
  return (
    <IonApp>
      <IonReactRouter>
        <IonSplitPane contentId="main">
          <Menu />
          <IonRouterOutlet id="main">
            <Route path="/" exact={true}>
              <Redirect to="/page/clientes" />
            </Route>
            <Route path="/page/clientes" exact={true}>
              <ListaCliente />
            </Route>
            <Route path="/page/cliente/:id" exact={true}>
              <ClienteEditar />
            </Route>
            <Route path="/page/empleados" exact={true}>
              <ListaEmpleado />
            </Route>
            <Route path="/page/empleado/:id" exact={true}>
              <EmpleadoEditar />
            </Route>
            <Route path="/page/proveedores" exact={true}>
              <ListaProveedor />
            </Route>
            <Route path="/page/proveedor/:id" exact={true}>
              <ProveedorEditar />
            </Route>

          </IonRouterOutlet>
        </IonSplitPane>
      </IonReactRouter>
    </IonApp>
  );
};

export default App;
