import { IonButton, IonButtons, IonCard, IonCardContent, IonCardHeader, IonCol, IonContent, IonGrid, IonHeader, IonIcon, IonInput, IonItem, IonLabel, IonMenuButton, IonPage, IonRow, IonTitle, IonToolbar } from '@ionic/react';
import { add, checkmark, pencil, trash } from 'ionicons/icons';
import { useEffect, useState } from 'react';
import { useHistory, useParams, useRouteMatch } from 'react-router';
import ExploreContainer from '../../components/ExploreContainer';
import Empleado from './Empleado';
import { guardarEmpleado, searchEmpleadoById } from './EmpleadoApi';

const EmpleadoEditar: React.FC = () => {

  const { name } = useParams<{ name: string; }>();

  const [empleado, setEmpleado] = useState<any>({});
  const history = useHistory();

  const routeMatch:any = useRouteMatch("/page/empleado/:id");
  const id = routeMatch?.params?.id;


  //Se ejecuta esta función al momento de entrar en la página
  useEffect(()=>{
    search();
  },[history.location.pathname]);

  const search = async () =>{
    if(id == 'new') {
      setEmpleado({});
    }else{
      let result = await searchEmpleadoById(id);
      console.log("El empleado ",result);
      setEmpleado(result);
    }
  }

  const guardar = async () => {
    //provisional 
    //cliente.id = Math.round(Math.random()*10000);
    console.log(empleado);
    guardarEmpleado(empleado);
    history.push('/page/empleados')
  }



  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonButtons slot="start">
            <IonMenuButton />
          </IonButtons>
          <IonTitle>{name}</IonTitle>
        </IonToolbar>
      </IonHeader>

      <IonContent fullscreen>
        <IonHeader collapse="condense">
          <IonToolbar>
            <IonTitle size="large">{name}</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonCard>
          <IonCardHeader>
            <IonTitle>{id === 'new' ? 'Agregar empleado' : 'Editar empleado'}</IonTitle>
            <IonRow>
              <IonCol>
                <IonItem>
                  <IonLabel position="stacked">Nombre</IonLabel>
                  <IonInput onIonChange={e => empleado.nombre = String(e.detail.value)} value={empleado.nombre}></IonInput>
                </IonItem>
              </IonCol>
              <IonCol>
                <IonItem>
                  <IonLabel position="stacked">Email</IonLabel>
                  <IonInput
                    onIonChange={e => empleado.email = String(e.detail.value)}
                    value={empleado.email}>
                  </IonInput>
                </IonItem>
              </IonCol>
            </IonRow>
            <IonRow>
              <IonCol>
                <IonItem>
                  <IonLabel position="stacked">Teléfono</IonLabel>
                  <IonInput onIonChange={e => empleado.telefono = String(e.detail.value)} value={empleado.telefono}></IonInput>
                </IonItem>
              </IonCol>
              <IonCol>
                <IonItem>
                  <IonLabel position="stacked">Dirección</IonLabel>
                  <IonInput onIonChange={e => empleado.direccion = String(e.detail.value)} value={empleado.direccion}></IonInput>
                </IonItem>
              </IonCol>
            </IonRow>
            <IonRow>
              <IonCol>
                <IonItem>
                  <IonLabel position="stacked">Salario</IonLabel>
                  <IonInput onIonChange={e => empleado.salario = Number(e.detail.value)} value={empleado.salario}></IonInput>
                </IonItem>
              </IonCol>
              <IonCol>
                <IonItem>
                  <IonButton onClick={guardar} color='success' slot='end'>
                    <IonIcon icon={checkmark} />
                    Guardar
                  </IonButton>
                </IonItem>
              </IonCol>
            </IonRow>

          </IonCardHeader>
          <IonCardContent>

          </IonCardContent>
        </IonCard>
      </IonContent>
    </IonPage>
  );
};

export default EmpleadoEditar;
