import { APP_PROVEEDORES } from "../../variables/variables";
import Proveedor from "./Proveedor";

export async function buscarProveedores(){
    let url= APP_PROVEEDORES + 'proveedores';
    let response = await fetch(url, {
        "method":'GET',
        "headers": {
            "Content-Type": 'application/json'
        }
    })

    let proveedores = (await response).json();
    return proveedores;
}

export async function eliminarProveedor(id:string){
    let url= APP_PROVEEDORES + 'proveedores/' + id;
    await fetch(url, {
        "method":'DELETE',
        "headers": {
            "Content-Type": 'application/json'
        }
    });
}

export async function guardarProveedor(proveedor:Proveedor){
    let url= APP_PROVEEDORES + 'proveedores';
    await fetch(url, {
        "method":'POST',
        "body":JSON.stringify(proveedor),
        "headers": {
            "Content-Type": 'application/json'
        }
    }); 
}

export async function searchProveedorById(id:string){
    let url= APP_PROVEEDORES + 'proveedores/' + id;
    let response = await fetch(url, {
        "method":'GET',
        "headers": {
            "Content-Type": 'application/json'
        }
    })

    let proveedor = (await response).json();
    return proveedor;
}